import React, { useEffect, useState } from "react";
import { addItemtoCart, removeItemFromCart } from "./helper/cartHelper";
import ImageHelper from "./helper/ImageHelper";

const CartModal = ({ item, reload = undefined, setReload = (f) => f }) => {
  var [count, setCount] = useState(item.count);
  const [subTotal, setSubTotal] = useState(item.price);

  const handleMinus = (price) => {
    setCount(count == 0 ? 0 : (count -= 1));
    addItemtoCart(item, count);
    setSubTotal(count * price);
    setReload(!reload);
  };

  const handlePlus = (price) => {
    setCount((count += 1));
    addItemtoCart(item, count);
    setSubTotal(count * price);
    setReload(!reload);
  };

  const removeItem = () => {
    removeItemFromCart(item._id);
    setReload(!reload);
  };

  return (
    <div className="cart-container md:grid md:grid-cols-4 mx-5 lg:mx-10 my-5 bg-gray-100 rounded-2xl shadow-lg">
      <div className="h-52 w-52 overflow-hidden md:col-span-1">
        <ImageHelper product={item} />
      </div>
      <div className="card-section py-2 lg:py-4 md:col-span-2 px-5">
        <h2 className="card-title text-xl font-extrabold tracking-wider">
          {item.name}
        </h2>
        <p className="lead text-lg font-light tracking-wide">
          {item.description}
        </p>
        <div className="tracking-widest font-bold ">
          <span className="text-primary-900">₹ {item.price}</span> 1N
        </div>
        <div className="md:mt-5 -ml-3">
          <button
            className="login-btn inline border-red-300 hover:bg-red-400 hover:text-white"
            onClick={removeItem}
          >
            Remove
          </button>
        </div>
      </div>
      <div className="item-price md:col-span-1 md:pr-10 p-2 lg:py-1 flex flex-col md:flex-row md:flex-col justify-around md:items-end">
        <div className="tracking-widest font-bold ">
          Subtotal: <span className=" text-primary-900">₹{subTotal}</span>
        </div>
        <div className="item-count self-end">
          <button
            className="border-2 p-2 px-3 border-gray-300 focus:outline-none"
            onClick={() => handleMinus(item.price)}
          >
            -
          </button>
          <span className="border-2 p-2 px-3 bg-gray-200 border-gray-300">
            {count}
          </span>
          <button
            className="border-2 p-2 px-3 border-gray-300 focus:outline-none"
            onClick={() => handlePlus(item.price)}
          >
            +
          </button>
        </div>
      </div>
    </div>
  );
};

export default CartModal;
