import { Dialog, DialogContent, DialogTitle } from "@material-ui/core";
import React, { useEffect, useState } from "react";

import CartModal from "./CartModal";
import { loadCart } from "./helper/cartHelper";

const Cart = () => {
  let amt = 0;
  const [purchased, setPurchased] = useState("");
  const [reload, setReload] = useState(false);
  const [curr, setCurr] = useState(true);
  const [grandTotal, setGrandTotal] = useState(0);
  const [openDailog, setOpenDailog] = useState(false);

  const handleTotal = () => {
    purchased &&
      purchased.map((item) => {
        // console.log(item.name, item.count);
        amt += item.price * item.count;
      });
    setCurr(!curr);
    setGrandTotal(amt);
  };

  const handleClose = () => {
    setOpenDailog(false);
  };

  const handleDailog = () => {
    if (openDailog) {
      return (
        <Dialog open={openDailog} onClose={handleClose}>
          <DialogTitle className="flex justify-center">
            <span className="text-3xl font-bold tracking-wider uppercase text-body">
              Call Us
            </span>
          </DialogTitle>
          <DialogContent>
            <p className="text-body text-xl">
              Your Total amount:{" "}
              <span className="text-primary-900 font-bold tracking-wider">
                ₹{grandTotal}
              </span>
            </p>
            <p className="text-body text-3xl tracking-wider font-bold">
              +91 9414523569
            </p>
          </DialogContent>
        </Dialog>
      );
    }
  };

  const buyProduct = () => {
    console.log("BUY");
    setOpenDailog(!openDailog);
  };

  useEffect(() => {
    handleTotal();
  }, [curr]);

  useEffect(() => {
    setPurchased(loadCart());
    handleTotal();
  }, [reload]);

  return (
    <div className="w-64 md:w-max mx-5 lg:mx-10 text-body">
      <div className="header flex justify-between items-center">
        <h1 className="text-2xl md:text-4xl font-bold md:mb-5 py-5 uppercase">
          {purchased.length !== 0 ? "Your Cart" : "Your Cart is Empty."}
        </h1>
        {purchased.length !== 0 && (
          <div className="flex flex-col md:flex-row justify-center md:items-center">
            <div className="total">
              <span className="text-xl md:text-2xl font-bold">
                Cart Total:{" "}
                <span className="text-primary-700">₹{grandTotal}</span>
              </span>
            </div>

            <button
              className="signup-btn text-white inline"
              onClick={buyProduct}
            >
              Buy Now
            </button>
            {handleDailog()}
          </div>
        )}
      </div>
      <div>
        {purchased &&
          purchased.map((item) => (
            <CartModal item={item} reload={reload} setReload={setReload} />
          ))}
      </div>
    </div>
  );
};

export default Cart;
