const ProductEdit = (props) => {
  const productId = props.id;
  const [photo, setPhoto] = useState("");
  const getPhoto = () => {
    getProductPhoto(productId)
      .then((res) => {
        setPhoto(res);
        // console.log("PHOTO: ", res)
      })
      .catch((err) => console.log(err));
  };

  useEffect(() => {
    getPhoto();
  }, []);

  const handleChange = (e) => {
    const value = e.target.files[0];
    // setPhoto(value);
    console.log(value);
  };

  return (
    <Edit title="Edit Product" {...props}>
      <SimpleForm className="relative">
        <TextInput source="id" disabled />
        <TextInput source="name" />
        <TextInput multiline source="description" />
        <TextInput source="price" />
        <TextInput source="stock" />
        <ReferenceInput
          label="Category"
          source="category._id"
          reference="categories"
        >
          <SelectInput optionText="name" key="_id" />
        </ReferenceInput>
        <div className="lg:w-screen lg:h-screen lg:absolute lg:top-5 lg:left-1/3">
          <label>Photo Preview</label>
          <img src={photo} className="mt-3" />
        </div>

        <ImageInput source="photo" label="Photo" accept="image/*">
          <ImageField source="pic" />
        </ImageInput>
      </SimpleForm>
    </Edit>
  );
};
export default ProductEdit;
