import { Button, TextField, ThemeProvider } from "@material-ui/core";
import React, { useState } from "react";
import Base from "./Base";
import { postQuery } from "./helper/coreAPIcalls";
import theme from "./helper/materialHelper";

const Contact = () => {
  const [query, setQuery] = useState({
    name: "",
    email: "",
    message: "",
  });

  const handleChange = (name) => (event) => {
    setQuery({ ...query, [name]: event.target.value });
  };

  const handleQuery = (q) => {
    postQuery(q).then(() =>
      setQuery({ ...query, name: "", email: "", message: "" })
    );
  };

  //components
  const formStructure = () => (
    <form className="flex flex-col w-4/6 ml-10 h-64 justify-evenly">
      <ThemeProvider theme={theme}>
        <TextField
          label="Name"
          value={query.name}
          onChange={handleChange("name")}
        />
        <TextField
          label="Email Address"
          required
          type="email"
          id="email"
          value={query.email}
          onChange={handleChange("email")}
          //TODO: conditional rendering is to be done
          // error
          // helperText="Invalid email"
        />
        <TextField
          label="Message"
          multiline
          rows={3}
          value={query.message}
          onChange={handleChange("message")}
        />
        <div className="ml-auto">
          <Button
            size="medium"
            variant="contained"
            color="primary"
            onClick={() => handleQuery(query)}
          >
            Send
          </Button>
        </div>
      </ThemeProvider>
      {/* {JSON.stringify(query)} */}
    </form>
  );

  return <Base heading="Contact US">{formStructure()}</Base>;
};

export default Contact;
