import React from "react";
import "./core.css";
import cardImage from "../assets/home.svg";
import AddShoppingCartIcon from "@material-ui/icons/AddShoppingCart";
import ImageHelper from "./helper/ImageHelper";
import { isAuthenticated } from "../auth/helper";
import { addItemtoCart } from "./helper/cartHelper";

const Card = ({ product, reload = undefined, setReload = (f) => f }) => {
  const cardTitle = product ? product.name : "A photo from pexels";
  const cardDescription = product ? product.description : "A Description";
  const cardPrice = product ? product.price : "5";

  const addProduct = () => {
    addItemtoCart(product);
    setReload(!reload);
  };

  return (
    <div className="flex flex-col mt-10 mx-8 shadow-xl bg-gray-100">
      <div className="img-container h-min w-full">
        <ImageHelper product={product} />
      </div>
      <div className="card-section z-1 bg-gray-100 h-36 w-full rounded-2xl rounded-b-sm relative -top-3 flex flex-col justify-evenly">
        <h2 className="title text-xl font-extrabold tracking-wider ml-5">
          {cardTitle}
        </h2>
        <p className="lead text-lg font-light tracking-wide ml-5">
          {cardDescription}
        </p>
        <div className="buy flex flex-row justify-between ml-5">
          <span
            id="price"
            className="tracking-widest text-primary-900 font-bold"
          >
            ₹ {cardPrice}
          </span>
          {isAuthenticated() && (
            <div>
              <button
                className="btn login-btn inline px-4 py-2 mx-4 my-1 hover:bg-gray-300 hover:border-primary-900 text-body"
                onClick={addProduct}
                id="cartBtn"
              >
                <span className="hidden xl:inline-block">Add to</span>
                <AddShoppingCartIcon fontSize="small" className="ml-1" />
              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default Card;
