import React from "react";
import { Admin, Resource } from "react-admin";

import dataProvider from "./helper/myDataProvider";
import { API } from "../backend";
import { fetchJson as httpClient } from "./helper/httpClient";
import theme from "../core/helper/materialHelper";

import ProductList, { CategoryList, QueryList, UserList } from "./helper/List";
import ProductCreate, { CategoryCreate } from "./helper/Create";
import ProductEdit, { CategoryEdit, QueryEdit, UserEdit } from "./helper/Edit";

import ClassIcon from "@material-ui/icons/Class";
import CategoryIcon from "@material-ui/icons/Category";
import PeopleIcon from "@material-ui/icons/People";
import MyLayout from "./helper/MyLayout";

const myDataProvider = dataProvider(`${API}`, httpClient);

const AdminDashboard = () => {
  return (
    <Admin layout={MyLayout} dataProvider={myDataProvider} theme={theme}>
      <Resource
        name="products"
        list={ProductList}
        create={ProductCreate}
        edit={ProductEdit}
        icon={ClassIcon}
      />
      <Resource
        name="categories"
        list={CategoryList}
        edit={CategoryEdit}
        create={CategoryCreate}
        icon={CategoryIcon}
      />
      <Resource
        name="users"
        list={UserList}
        edit={UserEdit}
        icon={PeopleIcon}
      />
      <Resource name="queries" list={QueryList} icon={ClassIcon} />
    </Admin>
  );
};

export default AdminDashboard;
