import React, { useState } from "react";

import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";

import DialogTitle from "@material-ui/core/DialogTitle";
import { CircularProgress, ThemeProvider } from "@material-ui/core";
import theme from "./helper/materialHelper";
import { authenticate, isAuthenticated, signin, signup } from "../auth/helper";
import { Redirect, withRouter } from "react-router-dom";
import SimpleSnackbar from "./SnackBar";

const Modal = ({ title, toggleModal, history }) => {
  const [open, setOpen] = useState(true);
  const [toast, setToast] = useState(false);

  const [values, setValues] = useState({
    email: "",
    name: "",
    password: "",
    error: "",
    success: false,
    redirect: false,
    loading: false,
  });

  const { email, name, password, error, success, redirect, loading } = values;

  const { user } = isAuthenticated();

  //  togglers
  toggleModal(open);

  const flagToast = () => {
    setToast(!toast);
  };

  //  handlers
  const handleChange = (name) => (event) => {
    setValues({ ...values, error: false, [name]: event.target.value });
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleKeyUp = (e) => {
    if (e.keyCode === 13) {
      e.preventDefault();
      document.querySelector("#submitBtn").click();
    }
  };

  const performRedirect = () => {
    if (redirect) {
      if (user && user.role === 1) {
        return <Redirect to="/admin/dashboard" />;
      } else {
        return (
          <div>
            <Redirect to="/" />
          </div>
        );
      }
    }
  };

  const onSubmit = (event) => {
    event.preventDefault();
    setValues({ ...values, error: false, loading: true });

    if (title === "Log In") {
      signin({ email, password })
        .then((data) => {
          if (data.error) {
            setValues({
              ...values,
              error: data.error,
              success: false,
              loading: false,
            });
          } else {
            authenticate(data, () => {
              setValues({
                ...values,
                email: "",
                password: "",
                error: "",
                success: true,
                redirect: true,
                loading: false,
              });
            });
          }
        })
        .catch((err) => console.log("sign in request failed"));
    } else {
      signup({ name, email, password })
        .then((data) => {
          if (data.errors) {
            setValues({
              ...values,
              error: data.errors,
              success: false,
              loading: false,
            });
          } else {
            setValues({
              ...values,
              name: "",
              email: "",
              password: "",
              error: "",
              success: true,
              redirect: true,
              loading: false,
            });
          }
        })
        .catch((err) => console.log("Error in signup"));
    }
    flagToast();
    {
      setTimeout(() => {
        handleClose();
      }, 2000);
    }
  };

  //  components
  const successMsg = (success) => {
    if (success) {
      return (
        <SimpleSnackbar
          type="success"
          text={
            title === "Log In"
              ? "Logged in successfully"
              : "Signed Up successfully"
          }
          openToast={toast}
        />
      );
    } else {
      return (
        <SimpleSnackbar
          type="error"
          text={title === "Log In" ? `${error}` : `${error}`}
          openToast={toast}
        />
      );
    }
  };

  const modalForm = () => (
    <div>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title" className="flex justify-center">
          <span className="text-3xl font-bold tracking-wider uppercase text-body">
            {title}
          </span>
        </DialogTitle>
        <ThemeProvider theme={theme}>
          <DialogContent>
            <TextField
              autoFocus
              margin="dense"
              id="email"
              label="Email Address"
              type="email"
              variant="outlined"
              fullWidth
              onChange={handleChange("email")}
              value={email}
            />
            {title === "Sign Up" && (
              <TextField
                margin="dense"
                id="name"
                label="Name"
                type="text"
                variant="outlined"
                fullWidth
                onChange={handleChange("name")}
                value={name}
              />
            )}
            <TextField
              margin="dense"
              id="password"
              label="Password"
              type="password"
              variant="outlined"
              fullWidth
              onChange={handleChange("password")}
              value={password}
              onKeyUp={handleKeyUp}
            />
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose} color="primary" variant="text">
              Cancel
            </Button>

            <Button
              onClick={onSubmit}
              variant="contained"
              color="primary"
              id="submitBtn"
            >
              {title}
            </Button>
          </DialogActions>
        </ThemeProvider>
      </Dialog>
    </div>
  );

  const loadingMsg = () => {
    if (loading) {
      return (
        <ThemeProvider theme={theme}>
          <CircularProgress color="primary" />
        </ThemeProvider>
      );
    }
  };

  return (
    <div>
      {loadingMsg()}
      {modalForm()}
      {successMsg(success)}
      {performRedirect()}
    </div>
  );
};

export default withRouter(Modal);
