import React, { useState } from "react";
import NavItem from "./NavItem";

import { Link, withRouter } from "react-router-dom";

import { isAuthenticated, signout, isAdmin } from "../auth/helper/index";

import HomeIcon from "@material-ui/icons/Home";
import HelpIcon from "@material-ui/icons/Help";
import MailIcon from "@material-ui/icons/Mail";
import PersonIcon from "@material-ui/icons/Person";
import PersonAddIcon from "@material-ui/icons/PersonAdd";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import ShoppingCartOutlinedIcon from "@material-ui/icons/ShoppingCartOutlined";
import MenuIcon from "@material-ui/icons/Menu";
import IconButton from "@material-ui/core/IconButton";

import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import Divider from "@material-ui/core/Divider";

import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";

import SwipeableDrawer from "@material-ui/core/SwipeableDrawer";
import Drawer from "@material-ui/core/Drawer";
import Modal from "./Modal";
import { Badge, ThemeProvider } from "@material-ui/core";
import theme from "./helper/materialHelper";
import SimpleSnackbar from "./SnackBar";
import Footer from "./Footer";
import Cart from "./Cart";
import { getTotalItem, loadCart } from "./helper/cartHelper";
import { useEffect } from "react";

const currentTab = (history, path) => {
  if (window.location.pathname === path) {
    return true;
  } else {
    return false;
  }
};

const useStyles = makeStyles({
  list: {
    width: 250,
  },
  fullList: {
    width: "auto",
  },
});

const Base = ({ heading, children, history, reload, setReload = (f) => f }) => {
  const classes = useStyles();
  const [openLeft, setOpenLeft] = useState(false);
  const [openRight, setOpenRight] = useState(false);
  const [cart_item, setCart_item] = useState(0);
  const [modal, setModal] = useState(false);
  const [toast, setToast] = useState(false);
  const [btn, setBtn] = useState("");

  const [type, setType] = useState("");
  const [text, setText] = useState("");

  const toggleDrawer = (side, open) => (event) => {
    if (!isAuthenticated()) {
      setToast(!toast);
      setType("warning");
      setText("Please Login");
      return;
    }
    if (
      event &&
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }
    side === "left" ? setOpenLeft(open) : setOpenRight(open);
  };

  const list = () => (
    <div
      className={clsx(classes.list, {})}
      role="presentation"
      onClick={toggleDrawer("left", false)}
      onKeyDown={toggleDrawer("left", false)}
    >
      <Link to="/">
        <div className="p-2 m-2 flex justify-center items-center text-2xl font-bold cursor-pointer">
          <span className="text-primary-500">V</span>inayak{" "}
          <span className="text-primary-500">TM</span>
        </div>
      </Link>

      <List>
        <Link to="/">
          <ListItem>
            <ListItemIcon>
              <HomeIcon />
            </ListItemIcon>
            <ListItemText primary="Home" />
          </ListItem>
        </Link>

        <Link to="/about">
          <ListItem>
            <ListItemIcon>
              <HelpIcon />
            </ListItemIcon>
            <ListItemText primary="About Us" />
          </ListItem>
        </Link>

        <Link to="/contact">
          <ListItem>
            <ListItemIcon>
              <MailIcon />
            </ListItemIcon>
            <ListItemText primary="Contact Us" />
          </ListItem>
        </Link>
      </List>

      <Divider />
      <List>
        {!isAuthenticated() && (
          <div>
            <button
              className="btn login-btn block px-5 py-1 mb-4 mx-auto"
              onClick={() => flagModal()}
            >
              <PersonIcon className="mr-1" />
              Log In
            </button>
            <button
              className="btn signup-btn block px-5 py-1 mx-auto"
              onClick={() => flagSignUpModal()}
            >
              <PersonAddIcon className="mr-1" />
              Sign Up
            </button>
          </div>
        )}
        {isAuthenticated() && (
          <div>
            {isAdmin() && (
              <Link to="/admin/dashboard">
                <button
                  id="adminBtn"
                  className="btn signup-btn block px-5 py-1 mb-4 mx-auto"
                  href="/admin/dashboard"
                >
                  <PersonIcon className="mr-1" />
                  Admin Dashboard
                </button>
              </Link>
            )}
            <button
              id="signOutBtn"
              className="btn login-btn block px-5 py-1 mb-4 mx-auto"
              onClick={() => {
                signout(() => {
                  setToast(!toast);
                  history.push("/");
                });
              }}
            >
              <ExitToAppIcon className="mr-1" />
              Sing Out
            </button>
          </div>
        )}
      </List>
    </div>
  );

  const cart = () => {
    return <Cart reload={reload} setReload={setReload} />;
  };

  const flagModal = () => {
    setModal(!modal);
    const loginBtn = document.querySelector("#loginBtn").innerText;
    setBtn(loginBtn);
  };

  const flagSignUpModal = () => {
    setModal(!modal);
    const signupBtn = document.querySelector("#signUpBtn").innerText;
    setBtn(signupBtn);
  };

  const showModal = (open) => {
    setModal(open);
  };

  //Components
  const navbar = () => (
    <nav
      id="nav-container"
      className="text-opacity-10 sticky top-0 hidden md:block"
    >
      <Link to="/">
        <NavItem
          name="Home"
          icon={<HomeIcon />}
          isActive={currentTab(history, "/")}
        />
      </Link>
      <Link to="/about">
        <NavItem
          name="About US"
          icon={<HelpIcon />}
          isActive={currentTab(history, "/about")}
        />
      </Link>
      <Link to="/contact">
        <NavItem
          name="Contact US"
          icon={<MailIcon />}
          isActive={currentTab(history, "/contact")}
        />
      </Link>
    </nav>
  );

  const swipeableDrawer = (type, anchor, open, show) => {
    if (type === "swipeable") {
      return (
        <SwipeableDrawer
          anchor={anchor}
          open={open}
          onClose={toggleDrawer(anchor, false)}
          onOpen={toggleDrawer(anchor, true)}
        >
          {show()}
        </SwipeableDrawer>
      );
    } else {
      return (
        <Drawer
          anchor={anchor}
          open={open}
          onClose={toggleDrawer(anchor, false)}
          onOpen={toggleDrawer(anchor, true)}
        >
          {show()}
        </Drawer>
      );
    }
  };

  const handleCartItem = () => {
    setCart_item(getTotalItem());
  };

  const handleSignout = () => {
    signout(() => {
      setToast(!toast);
      history.push("/");
      setType("success");
      setText("Signout success");
    });
  };

  useEffect(() => {
    handleCartItem();
  }, [reload]);
  return (
    <div className="md:grid md:grid-cols-7 sm:grid-cols-4 h-screen w-full ">
      <div
        id="navbar"
        className="md:col-span-1 sm:col-span-1 md:border-r md:block flex flex-row items-center"
      >
        <span className="md:hidden">
          <IconButton onClick={toggleDrawer("left", true)}>
            <MenuIcon />
          </IconButton>
        </span>

        <Link to="/">
          <div
            id="brand"
            className="flex lg:justify-center items-center p-2 lg:p-4 m-2 lg:m-4 text-2xl lg:text-4xl font-bold cursor-pointer"
          >
            <span className="text-primary-700">V</span>inayak{" "}
            <span className="text-primary-700">TM</span>
          </div>
        </Link>

        {navbar()}
      </div>
      {swipeableDrawer("swipeable", "left", openLeft, list)}

      <div className="main md:col-span-6 sm:col-span-3">
        <header className="header md:h-24 flex items-center justify-end">
          <div className="right_section flex flex-row justify-end items-center">
            {!isAuthenticated() && (
              <div>
                <button
                  id="loginBtn"
                  className="btn login-btn"
                  onClick={() => {
                    flagModal();
                  }}
                >
                  <PersonIcon />
                  Log In
                </button>
                <button
                  id="signUpBtn"
                  className="btn signup-btn"
                  onClick={() => {
                    flagSignUpModal();
                  }}
                >
                  <PersonAddIcon className="mr-1" />
                  Sign Up
                </button>
              </div>
            )}

            {isAuthenticated() && (
              <div>
                {isAdmin() && (
                  <Link to="/admin/dashboard">
                    <button
                      id="adminBtn"
                      className="btn signup-btn"
                      href="/admin/dashboard"
                    >
                      <PersonIcon className="mr-1" />
                      Admin Dashboard
                    </button>
                  </Link>
                )}
                <button
                  id="signOutBtn"
                  className="btn login-btn"
                  onClick={handleSignout}
                >
                  <ExitToAppIcon className="mr-1" />
                  Sing Out
                </button>
              </div>
            )}
            {toast && (
              <SimpleSnackbar type={type} text={text} openToast={toast} />
            )}
            {modal && <Modal title={btn} toggleModal={showModal} />}

            <div
              className="cart m-5 mr-10 md:relative md:top-0 md:right-0 cursor-pointer absolute -top-1 -right-1"
              onClick={toggleDrawer("right", true)}
            >
              <ThemeProvider theme={theme}>
                <Badge
                  badgeContent={isAuthenticated() ? cart_item : 0}
                  color="primary"
                >
                  <ShoppingCartOutlinedIcon
                    fontSize="large"
                    className="hover:text-gray-600"
                  />
                </Badge>
              </ThemeProvider>
            </div>
          </div>
          {isAuthenticated() &&
            swipeableDrawer("Drawer", "right", openRight, cart)}
        </header>

        <main id="main-section" className="pb-10 ">
          <div className="heading text-3xl md:text-5xl font-bold ml-10 md:mb-5 py-5 uppercase tracking-widest">
            {heading}
          </div>
          {children}
        </main>
        <Footer />
      </div>
    </div>
  );
};

export default withRouter(Base);
