import React from "react";
import {
  List,
  Datagrid,
  TextField,
  EditButton,
  DeleteButton,
  Filter,
  TextInput,
  EmailField,
  BooleanField,
  ChipField,
  useListContext,
} from "react-admin";

const CategoryFilter = (props) => (
  <Filter {...props}>
    <TextInput variant="outlined" label="Search" source="q" alwaysOn />
    <TextInput variant="outlined" label="Name" source="name" defaultValue="" />
  </Filter>
);
export const UserList = (props) => {
  return (
    <List {...props}>
      <Datagrid>
        <TextField source="id" />
        <TextField source="name" />
        <TextField source="lastname" />
        <EmailField source="email" />
        <BooleanField source="role" label="Admin" />
        <EditButton basePath="/users" />
        <DeleteButton basePath="/users" />
      </Datagrid>
    </List>
  );
};
export const CategoryList = (props) => {
  return (
    <List
      {...props}
      filters={<CategoryFilter />}
      sort={{ field: "price", order: "ASC" }}
    >
      <Datagrid>
        <TextField source="id" />
        <TextField source="name" />
        <EditButton basePath="/categories" />
        <DeleteButton basePath="/categories" />
      </Datagrid>
    </List>
  );
};

const ProductList = (props) => {
  return (
    <List {...props} filters={<CategoryFilter />}>
      <Datagrid>
        {/* {console.log("PROPS: ", props)} */}
        <TextField source="id" />
        <TextField source="name" />
        <TextField source="description" />
        <TextField source="price" />
        <TextField source="stock" />
        <ChipField source="category.name" label="Category" />
        <EditButton basePath="/products" />
        <DeleteButton basePath="/products" />
      </Datagrid>
    </List>
  );
};

export const QueryList = (props) => {
  return (
    <List {...props}>
      <Datagrid>
        <TextField source="name" />
        <TextField source="email" />
        <TextField source="message" />
        <DeleteButton basePath="/queries" />
      </Datagrid>
    </List>
  );
};

export default ProductList;
